.main {
  &__slider {
    position: relative;
  }
  &-slide {
    position: relative;
    color: #ffffff;
    background-image: url("../img/main-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 70px 0 440px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 30%;
      background-image: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.9) 200%);
      pointer-events: none;
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
    &__top {
      max-width: 1050px;
      display: grid;
      grid-template-columns: 100%;
      gap: 40px;
      p {
        font-size: 24px;
        line-height: 130%;
        color: #ffffff;
      }
    }
    h1 {
      font-family: Athelas;
      font-size: 93.7px;
      line-height: 120%;
      font-weight: 400;
      color: #ffffff;
    }
    &__btn {
      width: 300px;
      height: 70px;
      span {
        font-size: 22px;
      }
    }
    &__cards {
      max-width: 1130px;
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
      gap: 40px;
      margin-top: 60px;
      margin-left: auto;
    }
    &__card {
      justify-content: space-between;
      box-shadow: 0px 15px 40px 0px #163d61;
      &:after {
        background-image: linear-gradient(90.0deg, rgba(56,80,155,0.69) -24%, rgba(0,132,255,0.69) 125%) !important;
      }
      &-number {
        position: relative;
        z-index: 1;
        font-family: Athelas;
        font-size: 33px;
        line-height: 130%;
        color: #ffffff;
        margin-bottom: auto;
      }
      &-link {
        margin-top: 70px;
      }
    }
  }
  .swiper-button-next,
  .swiper-button-prev {
    top: 22%;
  }
  .swiper-pagination {
    left: max(calc(100% - 1530px) / 2);
    bottom: 410px;
    width: auto;
    text-align: start;
  }
}
.services {
  position: relative;
  z-index: 1;
  margin-top: -370px;
  &__inner {
    position: relative;
    padding: 60px 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% + 200px);
      background: #ffffff;
      border-radius: 60px 60px 0 0;
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin-top: 50px;
  }
  &__btn {
    display: none !important;
    width: 252px;
    height: 45px;
    margin: 30px auto 0;
    span {
      font-size: 13px;
    }
    i {
      flex-shrink: 0;
      width: 21px;
      margin-left: 12px;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}
.tours {
  position: relative;
  padding: 60px 0;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../img/tours-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    opacity: 0.2;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin-top: 50px;
  }
  &__btn {
    width: 410px;
    height: 70px;
    margin: 60px auto 0;
    span {
      font-size: 23px;
    }
    i {
      flex-shrink: 0;
      width: 34px;
      margin-left: 25px;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}
.tour-card {
  position: relative;
  min-height: 325px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 25px;
  color: #ffffff;
  padding: 20px 30px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180.0deg, rgba(0,0,0,0.0) -9%, rgba(0,0,0,0.64) 110%);
    border-radius: 25px;
    pointer-events: none;
  }
  &:hover {
    &:after {
      background-image: linear-gradient(90.0deg, rgba(56,80,155,0.69) -24%, rgba(0,132,255,0.69) 125%);
    }
  }
  &__wrap {
    position: relative;
    z-index: 1;
  }
  &__title {
    font-size: 26px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    color: #ffffff;
    margin-top: 5px;
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px;
    margin-top: 16px;
    span {
      position: relative;
      font-size: 14px;
      line-height: 120%;
      color: #ffffff;
      opacity: 0.6;
    }
    span:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -9px;
      transform: translate(0, -50%);
      width: 3px;
      height: 3px;
      background: #ffffff;
      border-radius: 50%;
    }
  }
  &__date {
    display: flex;
    align-items: center;
    margin-top: 16px;
    i {
      flex-shrink: 0;
      width: 12px;
      margin-right: 10px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
    }
  }
  &__price {
    max-width: 246px;
    height: 60px;
    margin: 32px auto 0;
    span {
      font-size: 22px;
    }
  }
}


@media screen and (max-width: 1700px) {
  .main {
    &-slide {
      padding-left: 70px;
      padding-right: 70px;
    }
    .swiper-pagination {
      left: 100px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .tour-card {
    min-height: auto;
    padding: 40px 20px 20px;
    &__price {
      span {
        font-size: 20px;
      }
    }
  }
}

