      bottom: 270px;
    }
  }
  .services {
    margin-top: -240px;
  }
  .tours {
    &__btn {
      width: 252px;
      height: 45px;
      margin-top: 30px;
      span {
        font-size: 14px;
      }
      i {
        width: 21px;
      }
    }
  }
  .tour-card {
    &__info {
      gap: 5px 15px;
    }
    &__price {
      margin-top: 16px;
    }
  }
}

@media screen and (max-width: 450px) {
  .services {
    &__cards {
      max-width: 225px;
      grid-template-columns: 100%;
      gap: 16px;
      margin-left: auto;
      margin-right: auto;
    }
    &-card:nth-child(5) ~ .services-card {
      display: none;
    }
    &__btn {
      display: flex !important;
    }
  }
  .tours {
    &__cards {
      max-width: 225px;
      grid-template-columns: 100%;
      margin-left: auto;
      margin-right: auto;
    }
    &__btn {
      span {
        font-size: 13px;
      }
    }
  }
  .tour-card:nth-child(2) ~ .tour-card {
    display: none;
  }
}